@media (prefers-color-scheme: dark) {
    // .separator {
    //     background-image: url(/img/asterisco-dark.svg) !important;
    // }
    
    article {

        h2::before {
            background-image: url(/img/asterisco-dark.svg) !important;
        }
    }

    .manuale {
        #cork-board {
            article {
                background-color: $dark_background_color;
                border-color: #fff;
            }

        }
        
        #tag-selector {
            .tag-button {
                background-color: $dark_background_color;
                border: 1px solid white;
                box-shadow: 5px 5px 0 rgb(224, 224, 224);
             
                &:hover {
                    border-color: #000;
                }
            }
        }
    }
}