.manuale {
    max-width: $max_width;

    #cork-board {
        
        .quote {
            background-color: #f6f6f6;
            border: 1px solid black;
            padding: 1rem;
            box-shadow: 2px 2px 0px #ddd;
            
            p {
                margin-top: 0;
            }
            
            .who {
                text-align: right;
                font-size: .8rem;
            }
            
            .link {
                float: left;
                font-size: 0.6rem;
                position: relative;
                top: 0.4rem;
                display: none;
            }

            &:hover .link {
                display: block;
            }
        }
    }

    #tag-selector {
        margin-top: 2rem;

        #feeling-lucky, .tag-button {
            border: 1px solid black;
            background-color: $background_color;
            display: inline-block;
            padding: 0.2rem 1rem;
            font-family: $mono_fonts;
            font-size: .8rem;
            box-shadow: 5px 5px 0 black;
            margin: 5px;
            margin-left: 0;
            cursor: pointer;
            
            &:hover {
                background-color: #ffd500;
                color: #000;
                box-shadow:5px 5px 0 $hover_link;
            }
            
            &:active {
                position: relative;
                top: 5px;
                left: 5px;
                box-shadow: none;
            }
            
            &.active-filter {
                position: relative;
                top: 5px;
                left: 5px;
                box-shadow: none;
                background-color: #ddd;
                color: #000;
            }
            
            &#tag-tutti {
                font-weight: bold;
            }
        }

        #feeling-lucky {
            background-color: #c40000;
            color: #fff;
            font-weight: bold;
        }
    }
}

article.manuale {
    background-color: #f6f6f6;
    border: 1px solid black;
    padding: 1rem;
    box-shadow: 2px 2px 0px #ddd;
    
    p {
        margin-top: 0;
    }
    
    .who {
        text-align: right;
        font-size: .8rem;
    }

    #meta, h1 {
        display: none;
    }
}

.consigli-scrittura {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    .consiglio {
        display: block;
        width: 8rem;
        height: 6rem;
        font-weight: bolder;
        border: 1px solid #fff;
        background-color: #000;
        padding: 0.2rem 1rem;
        font-family: $mono_fonts;
        font-size: 1.4rem;
        box-shadow: 5px 5px 0 #ccc;
        margin: 15px;
        margin-left: 0;
        position: relative;
        color: #fff;

        .title {
            position: absolute;
            bottom: .4rem;
        }

        &:hover {
            background-color: #ffd500;
            color: #000;
            box-shadow:5px 5px 0 $hover_link;
        }
            
        &:active {
            position: relative;
            top: 5px;
            left: 5px;
            box-shadow: none;
        }
    }
}

#go-back {
    font-size: .8rem;

    &::before {
        content: "←";
    }
}

@media only screen and (max-width: 720px) {
    article {
        margin-left: 0;
        margin-right: 0;
    }
}
